.fakeScreen {
    background-color: #151515;
    box-sizing: border-box;
    width: 50%;
    margin: auto;
    margin-top: 1rem;
    padding: 0;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  .p {
    margin-left: 1rem;
    font-size: 1.25em;
    font-family: monospace;
    white-space: nowrap;
    overflow: hidden;
    width: 0;
  }
  
  span {
    color: #fff;
    font-weight: bold;
  }
  
  .line1 {
    color: coral;
    -webkit-animation: type 2s 2s steps(30, end) forwards;
    animation: type 2s 2s steps(30, end) forwards;
  }
  
  .cursor1 {
    -webkit-animation: blink 1s 2s 2 forwards;
    animation: blink 1s 2s 2 forwards;
  }
  
  .line2 {
    color: coral;
    -webkit-animation: type 2s 4.25s steps(30, end) forwards;
    animation: type 2s 4.25s steps(30, end) forwards;
  }
  
  .cursor2 {
    -webkit-animation: blink 1s 5.25s 2 forwards;
    animation: blink 1s 5.25s 2 forwards;
  }
  
  .line3 {
    color: coral;
    -webkit-animation: type 2s 7.5s steps(30, end) forwards;
    animation: type 2s 7.5s steps(30, end) forwards;
  }
  
  .cursor3 {
    -webkit-animation: blink 1s 8.5s 2 forwards;
    animation: blink 1s 8.5s 2 forwards;
  }
  
  .line4 {
    color: coral;
    -webkit-animation: type 2s 10.75s steps(30, end) forwards;
    animation: type 2s 10.75s steps(30, end) forwards;
  }
  
  .cursor4 {
    -webkit-animation: blink 1s 8.5s 2 infinite;
    animation: blink 1s 8.5s 2 infinite;
  }

  .line5 {
    color: coral;
    -webkit-animation: type .5s 13.5s steps(30, end) forwards;
    animation: type .5s 13.5s steps(30, end) forwards;
  }
  
  .cursor5 {
    -webkit-animation: blink 1s 8.5s infinite;
    animation: blink 1s 8.5s infinite;
  }
  
  @-webkit-keyframes blink {
    0% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    90% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  @keyframes blink {
    0% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    90% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  @-webkit-keyframes type {
    to {
      width: 100%;
    }
  }
  
  @keyframes type {
    to {
      width: 100%;
    }
  }

  @media only screen and (max-width: 750px) {
    .fakeScreen {
        background-color: #151515;
        box-sizing: border-box;
        width: 90%;
        margin: auto;
        margin-top: 1rem;
        padding: 0;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
      }
  }


/* Selector bar with links  */
.bar {
  background: transparent;
  color: #999;
  width: 100%;
  align-content: center;
  text-align: center;
  padding-left: 0px;
  margin-bottom: 0;
  display: flex;
  height: 100%;
}

.chunk {
  padding-top: 2.5rem;
  padding-left: 0px;
  background: transparent;
  width: 33.3%;
  /* height: 10%; */
}

.chunk:hover {
  -webkit-transform: translate(5%, -5%);
          transform: translate(5%, -5%);
  transition: all .3s linear;
  color: transparent;
}

a {
  color: #999;
}
/* unvisited link */
a:link {
  color: #999;
}

/* visited link */
a:visited {
  color: #999;
}

/* mouse over link */
a:hover {
  color: #C44D33;
}

/* selected link */
a:active {
  color: #999;
}

@media only screen and (max-width: 750px) {
  .bar {
      display: block;
      margin-top: 5rem;
      margin-bottom: 5rem;
      bottom: 0;
      height: auto;
  }
  .chunk {
      width: 100%;
      padding-bottom: 2.5rem;
  }
}

@media only screen and (min-width: 750px) {
  .bar {
      position: fixed;
      bottom: 0;
      height: 7rem;
  }
}

