  /* Button Class */
.button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    background-color: #007bff; /* Blue background color */
    color: #fff; /* White text color */
    border: 2px solid #007bff; /* Blue border */
    border-radius: 4px; /* Rounded corners */
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease, -webkit-transform 0.3s ease;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
  }
  
  /* Button Hover Effect */
  .button:hover {
    background-color: #0056b3; /* Darker blue on hover */
    color: #fff; /* White text color on hover */
    -webkit-transform: scale(1.05);
            transform: scale(1.05); /* Scale up the button on hover */
  }
  
  /* Center the Button */
  .center-button {
    text-align: center;
  }

